import { describe, test, expect, jest } from '@jest/globals';
import { prisma } from '../../src/prisma';

describe('Simple Prisma Test', () => {
  test('prisma should be defined', () => {
    expect(prisma).toBeDefined();
    expect(prisma.curriculumExpectation).toBeDefined();
    expect(prisma.curriculumExpectation.findMany).toBeDefined();
  });
  
  test('can mock prisma methods', async () => {
    (prisma.curriculumExpectation.findMany as jest.Mock).mockResolvedValue([{ id: 'test' }]);
    const result = await prisma.curriculumExpectation.findMany();
    expect(result).toEqual([{ id: 'test' }]);
  });
});
